<template>
<div class="app-page page-content-bottom-menu" >
  <page-content class="page-content">
    <div class="item_box">
      <img src="static/images/personal/yz1.png" alt="">
      <div class="mid_content">
        <p class="title">新鲜紫茄子 绿色食品</p>
        <p class="m_title">规格:5kg</p>
        <div class="price_tag">
          <span>￥ 27.80</span>
          <p>x2</p>
        </div>
      </div>
    </div>
    <div class="title_line">
      <span></span>
      退款原因
    </div>
    <div class="quality_box">
      <div class="quality_item" v-for="(item, index) in 8" :key="index" >
        <div class="item_button"   @click="chooseOrder($event)" :id="item.id">
          质量问题
        </div>
      </div>
    </div>
    <div class="title_line">
      <span></span>
      问题描述
      <input type="text" placeholder="选填" class="qustion">
    </div>
    <div class="title_line" style="border-top: 15px solid #fafafa;">
      <span></span>
      凭证图片
    </div>
    <div class="vue-uploader">
      <div class="file-list">
        <section v-for="(file, index) of files"  class="file-item draggable-item" :key=index>
          <img :src="file.src" alt="" ondragstart="return false;" style="object-fit: cover;">
          <span class="file-remove" @click="remove(index)"></span>
        </section>
        <section v-if="status == 'ready'" class="file-item">
          <div @click="add" class="add">
            <img src="static/images/commonicon/pz.png" alt="" >
          </div>
        </section>
      </div>
      <input type="file" accept="image/*" @change="fileChanged" ref="file" multiple="multiple">
    </div>
    <div class="button_group">
      <div class="comment_button">
        申请退款
      </div>
      <div class="go_back">
        返回
      </div>
    </div>
  </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
export default {
  name: 'refundapplication',
  components: { PageContent, TitleSecond },
  props: {
    src: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      status: 'ready',
      files: [],
      point: {},
      uploading: false,
      percent: 0,
      active: ''
    }
  },
  methods: {
    chooseOrder: function (e) {
      if (e.target.className.indexOf('elected') === -1) {
        e.target.className = 'item_button selected' // 切换按钮样式
      } else {
        e.target.className = 'item_button'// 切换按钮样式
      }
    },

    add () {
      this.$refs.file.click()
    },
    // submit () {
    //   if (this.files.length === 0) {
    //     console.warn('no file!')
    //     return
    //   }
    //   const formData = new FormData()
    //   this.files.forEach((item) => {
    //     formData.append(item.name, item.file)
    //   })
    //   const xhr = new XMLHttpRequest()
    //   xhr.upload.addEventListener('progress', this.uploadProgress, false)
    //   xhr.open('POST', this.src, true)
    //   this.uploading = true
    //   xhr.send(formData)
    //   xhr.onload = () => {
    //     this.uploading = false
    //     if (xhr.status === 200 || xhr.status === 304) {
    //       this.status = 'finished'
    //       console.log('upload success!')
    //     } else {
    //       console.log(`error：error code ${xhr.status}`)
    //     }
    //   }
    // },
    finished () {
      this.files = []
      this.status = 'ready'
    },
    // 右上角删除图片
    remove (index) {
      this.files.splice(index, 1)
    },
    // 添加图片
    fileChanged () {
      const list = this.$refs.file.files
      for (let i = 0; i < list.length; i++) {
        if (!this.isContain(list[i])) {
          const item = {
            name: list[i].name,
            size: list[i].size,
            file: list[i]
          }
          this.html5Reader(list[i], item)
          this.files.push(item)
        }
      }
      this.$refs.file.value = ''
    },
    // 将图片文件转成BASE64格式
    html5Reader (file, item) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.$set(item, 'src', e.target.result)
      }
      reader.readAsDataURL(file)
    },
    isContain (file) {
      return this.files.find((item) => item.name === file.name && item.size === file.size)
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
@import '../../../styles/variables';
.page-content{
  .selected{
    color: #22ac38!important;
    border: 1px  solid  #22ac38!important;
  }
  .active {
    background: #fd7522;
    border: 1px solid #fd7522;
    color: #fff;
  }
  .item_box{
    background-color: #fafafa;
    padding: 30px 32px;
    display: flex;
    flex-direction: row;
    align-items: center;
    img{
      width: 150px;
      height: 150px;
    }
    .mid_content{
      font-size: 30px;
      margin-left: 30px;
      flex: 1;
      .title{
        font-weight: 600;
       }
      .m_title{
        margin: 30px 0;
        font-size: 20px;
        color: #999;
      }
      .price_tag{
        display: flex;
        flex-direction: row;
        align-items: center;
        span{
          color: #c8251d;
          flex: 1;
        }
        p{
          color: #666;
        }
      }
    }
  }
  .title_line{
    padding: 26px 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 24px;
    font-weight: 500;
    span{
      display: block;
      width: 10px;
      height: 28px;
      background-color: #22ac38;
      margin-right: 10px;
    }
    .qustion{
      margin-left: 30px;
     }
  }
  .quality_box{
    background-color: #fafafa;
    display: flex;
    flex-wrap: wrap;
    padding: 30px 50px;
    .quality_item{
      width: calc(100%/3);
      margin-bottom: 30px;
    }
    .item_button{
      margin: 0 auto;
      width: 160px;
      padding: 18px 20px;
      border-radius: 16px;
      border: solid 1px #999999;
      text-align: center;
      font-size: 26px;
      color: #666;
    }
  }
  .button_group{
    display: flex;
    flex-direction:row-reverse;
    margin-top: 30px;
    margin-right: 44px;
    .go_back,.comment_button{
      width: 150px;
      height: 54px;
      color: #666;
      border: 1px solid #999;
      border-radius: 14px;
      font-size: 26px;
      line-height:54px ;
      text-align: center;
    }
    .comment_button{
      color: #22ac38;
      border: solid 1px #22ac38;
      margin-left: 25px;
    }
  }
}
.vue-uploader {
  /* border: 1px solid #e5e5e5; */
  width: 100%;
  padding: 0 50px;
}
.vue-uploader .file-list {
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}
.vue-uploader .file-list:after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  font-size: 0;
}
.vue-uploader .file-list .file-item {
  width: 160px;
  height: 160px;
  background-color: #fff;
  margin-top: 10px;
  margin-left: 28px;
  position: relative;
  overflow: hidden;
  margin-right:28px;
}
.vue-uploader .file-list .file-item img{
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  width: 100%;
  height: 100%;
  max-width: 100%;

}
.vue-uploader .file-list .file-item .file-remove {
  position: absolute;
  right: 6px;
  display: none;
  top: 4px;
  width: 20px;
  height: 20px;
  background:#ddd;
  background-size: 20px 20px;
  cursor: pointer;
  line-height: 20px;
  border-radius: 100%;
  font-size: 2rem;
}
.vue-uploader .file-list .file-item:hover .file-remove {
  display: inline;
}
.vue-uploader .file-list .file-item .file-name {
  margin: 0;
  height: 40px;
  word-break: break-all;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.vue-uploader .add {
  width: 100%;
  height:100%;
  float: left;
  text-align: center;
  line-height: 80px;
  font-size: 30px;
  cursor: pointer;
}
.vue-uploader > input[type="file"] {
  display: none;
}
</style>



